<script setup lang="ts">
import { computed } from 'vue';
import { Icon } from '@/shared/ui/icons';
import { Circle } from '@/shared/ui/circle';
import { useRoute, RouterLink } from 'vue-router';
import { useSearch } from '@/features/header/search/model/search.model';

const {
    isInputVisible,
    toggleInputVisibility,
    searchQuery,
    hasSearchResults,
    productSearchResults,

} = useSearch();

const route = useRoute();
const isHomePage = computed(() => route.path === '/');
</script>

<template>
    <div class="search">
        <input type="text" v-model="searchQuery" :class="[
            'search-field',
            { 'search-field_visible': isInputVisible },
            { 'search-field-white-theme': !isHomePage }
        ]" placeholder="Введите текст для поиска" />

        <div :class="['search-results__container', { 'search-results-white-theme__container': !isHomePage }]"
            v-if="hasSearchResults && isInputVisible">
            <div class="search-results">
                <ul v-if="productSearchResults.length">
                    <li v-for="product in productSearchResults" :key="product.id">
                        <RouterLink :to="`/product/${product.slug}/`" @click="toggleInputVisibility">
                            {{ product.name }}
                        </RouterLink>
                    </li>
                </ul>

            </div>
        </div>

        <span @click="toggleInputVisibility" aria-label="Поиск" class="search-button">
            <Icon tagName="svg" color="dark" type="search" />
        </span>
    </div>
</template>

<style scoped lang="scss">
@import './style.scss';
</style>
