import { ref, onMounted } from 'vue';

export function useMapLogic() {
  const mapLoaded = ref(false);

  const initMap = () => {
    if (!window.ymaps) return;
    
    window.ymaps.ready(() => {
      const map = new window.ymaps.Map('map', {
        center: [53.917965, 27.558065], 
        zoom: 18,
        controls: [] 
      });

      const searchControl = new window.ymaps.control.SearchControl({
        options: {
          position: { }
        }
      });
      map.controls.add(searchControl);

      const trafficControl = new window.ymaps.control.TrafficControl({
        options: {
          position: { }
        }
      });
      map.controls.add(trafficControl);

      const typeSelector = new window.ymaps.control.TypeSelector({
        options: {
          position: {  }
        }
      });
      map.controls.add(typeSelector);
      
      const placemark = new window.ymaps.Placemark(
        [53.917965, 27.5578], 
        {
          hintContent: 'TopCvetok',
          balloonContent: 'Цветы в Минске'
        },
        {
          iconLayout: 'default#image',
          iconImageHref: 'https://cdn-icons-png.flaticon.com/512/684/684908.png',
          iconImageSize: [50, 50],
          iconImageOffset: [-20, -40]
        }
      );
      
      map.geoObjects.add(placemark);
      mapLoaded.value = true;
    });
  };

  onMounted(() => {
    if (!window.ymaps) {
      const script = document.createElement('script');
      script.src = 'https://api-maps.yandex.ru/2.1/?apikey=67dd4948-1c91-429a-a58d-48946c7759fa&lang=ru_RU';
      script.onload = initMap;
      document.head.appendChild(script);
    } else {
      initMap();
    }
  });

  return {
    mapLoaded
  };
}