<script setup lang="ts">
import { ref, computed, defineProps, defineEmits, onMounted } from 'vue';
import { Button } from '@/shared/ui/button';
import { Icon } from '@/shared/ui/icons';
import { Paragraph } from '@/shared/ui/text/paragraph';
import { Container } from '@/shared/ui/container';
import { Backdrop } from '@/shared/ui/backdrop'
import { API_BASE_URL } from '@/shared/api';
import { useRouter } from 'vue-router';
import axios from 'axios';

const router = useRouter();

const props = defineProps<{ serviceItems: { name?: string; promotion?: string; price?: number | string } | null }>();
const emit = defineEmits(['close']);

const name = ref('');
const phone = ref('');
const isAgreementChecked = ref(true);
const errors = ref({ name: '', phone: '' });

const validateName = () => {
    if (!name.value.trim()) {
        errors.value.name = ' ';
    } else if (!/^[А-Яа-яA-Za-z\s]+$/.test(name.value)) {
        errors.value.name = 'В имени допускаются только буквы и пробелы';
    } else {
        errors.value.name = '';
    }
};

const validatePhone = () => {
    const phonePattern = /^\+?\d{10,12}$/;
    if (!phone.value.trim()) {
        errors.value.phone = ' ';
    } else if (!phonePattern.test(phone.value)) {
        errors.value.phone = 'Некорректный номер';
    } else {
        errors.value.phone = '';
    }
};

const isFormValid = computed(() => {
    return (
        isAgreementChecked.value &&
        !errors.value.name &&
        !errors.value.phone &&
        name.value.trim() &&
        phone.value.trim()
    );
});

const handleSubmit = async () => {
    validateName();
    validatePhone();
 
    if (isFormValid.value) {
        try {
            const { data } = await axios.post(`${API_BASE_URL}request-note/`, {
                phone: phone.value,
                name: name.value,
                service_type: props.serviceItems?.name || 'без услуги',
            });
            name.value = '';
            phone.value = '';
            closeForm();
            router.push('/spasibo/');
        } catch (error: any) {
            console.error('Ошибка при отправке:', error?.response?.data || error.message);
        }
    }
};

const closeForm = () => {
    emit('close');
    document.body.classList.remove('no-scroll');
};

const toggleAgreement = () => {
    isAgreementChecked.value = !isAgreementChecked.value;
};

import { useOpenDocuments } from '@/shared/composables/useOpenDocuments';

const { openDocument } = useOpenDocuments();
onMounted(() => {
    document.body.classList.add('no-scroll');
});

</script>

<template>
    <Backdrop @click="closeForm">
        <Container >
            <div class="form-container" @click.stop>
                <span @click="closeForm" class="close-form-button">
                    <Icon type="crossBurger" color="map-dark" />
                </span>
                <div class="form-title" v-if="!serviceItems?.promotion">
                    <Paragraph tagName="p" color="accent" v-if="serviceItems?.name">
                        {{ serviceItems?.name }}
                        <Paragraph tagName="span" color="accent" v-if="serviceItems?.price">
                            {{ serviceItems?.price }}
                        </Paragraph>
                    </Paragraph>
                    <Paragraph tagName="p" color="dark">
                        Заполните Ваши данные и специалист свяжется с Вами в ближайшее время
                    </Paragraph>
                </div>
                <div class="form-title" v-else>
                    <Paragraph tagName="p" color="dark">
                        Заполните Ваши данные и получите подробную консультацию по услуге
                    </Paragraph>
                    <Paragraph tagName="p" color="accent" v-if="serviceItems?.promotion">
                        {{ serviceItems?.promotion }}
                    </Paragraph>
                </div>

                <form @submit.prevent="handleSubmit">
                    <div class="form-group">
                        <input type="text" v-model="name" @blur="validateName" placeholder="Укажите ваше имя"
                            :class="{ 'input-error': errors.name }" />
                        <p v-if="errors.name" class="error-text">{{ errors.name }}</p>
                    </div>
                    <div class="form-group">
                        <input type="tel" v-model="phone" @blur="validatePhone" placeholder="Укажите номер телефона"
                            :class="{ 'input-error': errors.phone }" />
                        <p v-if="errors.phone" class="error-text">{{ errors.phone }}</p>
                    </div>
                    <Button :disabled="!isFormValid" tagName="button" size="s" color="accent" type="submit" >
                    Отправить
                    </Button>
                    <div class="form-agreement">
                        <div class="checkbox" :class="{ 'checkbox-checked': isAgreementChecked }" @click="toggleAgreement">
                            <Icon type="tick" />
                        </div>
                        <Paragraph tagName="p" color="dark-gray">
                            Заполняя данную форму и нажимая на кнопку “Отправить”, я соглашаюсь с
                            <Paragraph tagName="span" color="accent" @click="openDocument('userAgreement')">
                                Пользовательским соглашением
                            </Paragraph>
                            и
                            <Paragraph tagName="span" color="accent" @click="openDocument('dataProcessingAgreement')">
                                Политикой в отношении обработки персональных данных
                            </Paragraph>.
                        </Paragraph>
                    </div>
                </form>
            </div>
        </Container>
    </Backdrop>
</template>


<style scoped lang="scss">
@import './style.scss';
</style>


