<script setup lang="ts">
import { onMounted, computed } from 'vue';
import { useStore } from 'vuex';
import { Icon } from '@/shared/ui/icons';

const store = useStore();

onMounted(() => {
  store.dispatch('contacts/fetchContacts');
});

const contact = computed(() => store.state.contacts?.contactsData?.[0] ?? null);

const hasLink = (link: string | null) => link && link.length > 1;
</script>

<template>
    <ul class="social-links" v-if="contact">
        <li v-if="hasLink(contact.instLink)">
            <a :href="`${contact.instLink}`" target="_blank" aria-label="Перейти в инстаграм">
                <Icon type="ig" color="none"></Icon>
            </a>
        </li>
        <li v-if="hasLink(contact.tgLink)">
            <a :href="`https://t.me/${contact.tgLink}`" target="_blank" aria-label="Написать в телеграмм" >
                <Icon type="TG" color="none"></Icon>
            </a>
        </li>
        <li v-if="hasLink(contact.vkLink)">
            <a :href="contact.vkLink" target="_blank" aria-label="Перейти на страницу вконтакте">
                <Icon type="VK" color="none"></Icon>
            </a>
        </li>
        <li v-if="hasLink(contact.fbLink)">
            <a :href="contact.fbLink" target="_blank" aria-label="Перейти на страницу фейсбук">
                <Icon type="FB" color="none"></Icon>
            </a>
        </li>
    </ul>
</template>

<style scoped lang="scss">
.social-links {
  list-style: none;
  display: flex;
  gap: 10px;

  li a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    border: 1px solid #CED0BB;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
  }
}
</style>
