import { ref, computed } from 'vue';

interface FormData {
    name?: string;
    price?: string;
    promotion?: string;
}

const isFormVisible = ref(false);
const formData = ref<FormData | null>(null);

export function useGlobalForm() {
    const openForm = (data: FormData = {}) => { 
        formData.value = data;
        isFormVisible.value = true;
    };

    const closeForm = () => {
        isFormVisible.value = false;
        formData.value = null;
    };

    const formattedFormData = computed(() => {
        if (!formData.value) return null;
        return {
            name: formData.value.name,
            price: formData.value.price,
            promotion: formData.value.promotion,
        };
    });

    return {
        isFormVisible,
        formattedFormData,
        openForm,
        closeForm,
    };
}
