import axios from 'axios';
import { Product} from './product.types';
import { API_BASE_URL } from '@/shared/api';

interface ProductsState {
    productsData: Product[];
    searchResults: Product[];
    
    currentPage: number; 
    casesPerPage: number; 
}

export const productsModule = {
    namespaced: true,
    state: (): ProductsState => ({
        productsData: [],
        searchResults: [],
        currentPage: 1,
        casesPerPage: 10,
    }),
    mutations: {
        setProducts(state: ProductsState, products: Product[] | null | undefined) {
            state.productsData = Array.isArray(products) ? products : [];
        },
        setSearchResults(state: ProductsState, results: Product[] | null | undefined) {
            state.searchResults = Array.isArray(results) ? results : [];
        },
    },
    
    actions: {
        async fetchProducts(
            { commit, state }: { commit: Function; state: ProductsState },
            { limit = 9, offset = 0 }: { limit?: number; offset?: number }
        ) {
            try {
                const response = await axios.get(
                    `${API_BASE_URL}products?limit=${limit}&offset=${offset}`
                  );
                  const products: Product[] = Array.isArray(response.data)
                    ? response.data
                    : response.data.results || [];
                  
                  if (offset === 0) {
                    commit('setProducts', products);
                  } else {
                    commit('setProducts', [...state.productsData, ...products]);
                  }
                  
            } catch (error) {
                console.error('Ошибка при получении данных:', error);
            }
        },
        searchProducts({ commit, state }: { commit: Function; state: ProductsState }, query: string) {
            const results = state.productsData.filter((product) => {
                const productName = `${product.name}`;
                /*const productColor = `${product.attributes.color}`*/
                
                return (
                    productName.toLowerCase().includes(query.toLowerCase())
                );
            });
            commit('setSearchResults', results);  
        },
    },
    getters: {
        getProductById: (state: ProductsState) => (id: string) => {
            return state.productsData.find((Product: Product) => Product.id === id) || null;
        },
        getProductBySlug: (state: ProductsState) => (slug: string) => {
            return state.productsData.find((Product: Product) => Product.slug === slug) || null;
        },
        searchResults(state: ProductsState) {
            return state.searchResults;
        },
    },
};
