import { ref, provide, inject, Ref } from 'vue';

export const useBurgerMenu = () => {
  const isVisibleMenu = ref(false);

  const openMenu = () => {
    isVisibleMenu.value = true;
  };

  const closeMenu = () => {
    isVisibleMenu.value = false;
  };

  provide('isVisibleMenu', isVisibleMenu);
  provide('openMenu', openMenu);
  provide('closeMenu', closeMenu);

  return { isVisibleMenu, openMenu, closeMenu };
};

export const useBurgerMenuInjection = () => {
  const isVisibleMenu = inject('isVisibleMenu') as Ref<boolean>;
  const openMenu = inject('openMenu') as () => void;
  const closeMenu = inject('closeMenu') as () => void;

  return { isVisibleMenu, openMenu, closeMenu };
};
