import { createStore } from 'vuex';
import { contactsModule } from '@/entities/contacts/model/contacts.store';
import { productsModule } from '@/entities/product/model/product.store';
import { reviewsModule } from '@/entities/reviews/model/reviews.store'
import { categoriesModule } from '@/entities/categories/model/categoies.store'
import { bannersModule } from '@/entities/banners/model/banners.store'

export const store = createStore({
  modules: {
    banners: bannersModule,
    categories: categoriesModule,
    products: productsModule,
    contacts: contactsModule,
    reviews: reviewsModule,
  },
  actions: {
    async searchAll({ dispatch }, query) {
      const results = {
        doctors: await dispatch('doctors/searchDoctors', query),
        cases: await dispatch('cases/searchCases', query),
        prices: await dispatch('prices/searchPrices', query),
      };
      return results;
    },
  },
});

