<script setup lang="ts">
import { Paragraph } from '@/shared/ui/text/paragraph';
import { onMounted, computed, ref, onBeforeUnmount } from 'vue';
import { useStore } from 'vuex';
import { RouterLink } from 'vue-router';

const store = useStore();
const activeIndex = ref(0);
let interval: ReturnType<typeof setInterval> | null = null;

const banners = computed(() => store.state.banners?.bannersData ?? []);

onMounted(() => {
  store.dispatch('banners/fetchBanners');

  interval = setInterval(() => {
    if (banners.value.length > 0) {
      activeIndex.value = (activeIndex.value + 1) % banners.value.length;
    }
  }, 7000);
});

onBeforeUnmount(() => {
  if (interval) clearInterval(interval);
});

const setActive = (index: number) => {
  activeIndex.value = index;
  if (interval) {
    clearInterval(interval);
    interval = setInterval(() => {
      activeIndex.value = (activeIndex.value + 1) % banners.value.length;
    }, 7000);
  }
};
</script>

<template>
  <section class="section-banners">
    <div class="section-banners__content">
      <RouterLink
        v-for="(banner, index) in banners"
        :key="banner?.id ?? index"
        :to="banner?.link || '#'"
        class="banner"
        :style="{ backgroundImage: `url(${banner?.photo || ''})` }"
        :class="{ active: index === activeIndex }"
      >
        <Paragraph tagName="p" color="white" size="xxxl" class="title">
          {{ banner?.title }}
        </Paragraph>
        <Paragraph tagName="p" color="white" size="xxxl" class="description">
          {{ banner?.text }}
        </Paragraph>
      </RouterLink>
    </div>

    <div class="indicators">
      <span
        v-for="(banner, index) in banners"
        :key="index"
        class="indicator"
        :class="{ active: index === activeIndex }"
        @click="setActive(index)"
      ></span>
    </div>
  </section>
</template>

<style lang="scss" scoped>
@import './style.scss';
</style>
