<script setup lang="ts">
import { Paragraph } from '@/shared/ui/text/paragraph';
import { RouterLink } from 'vue-router';
import { useStore } from 'vuex';
import { computed, onMounted } from 'vue';

const store = useStore();

onMounted(() => {
    store.dispatch('categories/fetchCategories');
});

const categories = computed(() => store.state.categories.categoriesData);

const navItems = computed(() => [
  {
    name: 'Каталог',
    link: '/product/',
    children: Array.isArray(categories.value)
      ? categories.value.map((cat: any) => ({
          name: cat.name,
          link: `/${cat.slug}/`
        }))
      : []
  },
  { name: 'Акции', link: '/product/akcii/' },
  { name: 'О нас', link: '/info/company/' },
  { name: 'Цветочная подписка', link: '/info/podpiska/' },
  { name: 'Доставка цветов', link: '/info/dostavka-cvetov/' },
  { name: 'Контакты', link: '/info/contacts/' }
]);

</script>

<template>
    <nav class="navigation">
        <ul class="navigation__list">
            <li v-for="(item, index) in navItems" :key="index" class="item">
                <RouterLink :to="item.link" class="item__link">
                    <Paragraph tag-name="span" size="xs" color="dark" class="item__text">
                        {{ item.name }}
                    </Paragraph>
                </RouterLink>

                <ul v-if="item.children && item.children.length" class="dropdown">
                    <li v-for="child in item.children" :key="child.link" class="dropdown__item">
                        <RouterLink :to="`/product${child.link}`" class="dropdown__link">
                            {{ child.name }}
                        </RouterLink>
                    </li>
                </ul>
            </li>
        </ul>
    </nav>
</template>

<style scoped lang="scss">
@import './style.scss';

.navigation {
    display: flex;
    align-items: center;
    position: relative;

    .navigation__list {
        display: flex;
        gap: 5px;

        .item {
            position: relative;

            &:hover {
                .dropdown {
                    display: grid;
                }

                .item__link {
                    text-decoration: underline var(--color-dark);
                }

                cursor: pointer;

            }

            .item__link {
                padding: 12px 30px;
            }

            .dropdown {
                display: none;
                grid-template-columns: repeat(5, 1fr);
                position: absolute;
                top: 100%;
                left: 0;
                background: white;
                border: 1px solid #eee;
                padding: 10px 0;
                min-width: 800px;
                background-color: #F1EFEB;
                padding: 20px;
                width: 100%;
                z-index: 10;

                &__item {
                    padding: 5px 15px;
                    white-space: nowrap;

                }

                &__link {
                    color: #333;
                    text-decoration: none;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}




@media screen and (max-width: 1222px) {
    .navigation {display: none;}
}
</style>
