<script setup lang="ts">
import { SliderCards } from '@/features/slider-cards'
import { Slider } from '@/shared/ui/slider';
import { useSwipe } from '@/shared/composables/useTouchSwipe'; 
import { ref, onMounted, watch } from 'vue';
import axios from 'axios';
import { API_BASE_URL } from '@/shared/api';

const currentPosition = ref(0);
const slideDistance = ref(395);
const isSliding = ref(false);

const updateWidth = (width: number) => {
  slideDistance.value = width + 10;
};
const slideLeft = () => {
  isSliding.value = true;
  currentPosition.value -= slideDistance.value;
  loadMoreIfNeeded();
  setTimeout(() => (isSliding.value = false), 300);
};

const slideRight = () => {
  isSliding.value = true;
  currentPosition.value += slideDistance.value;
  loadMoreIfNeeded();
  setTimeout(() => (isSliding.value = false), 300);
};

defineExpose({ slideLeft, slideRight });

interface Category {
  id: string;
  name: string;
  slug: string;
  full_slug: string;
}

interface Product {
  id: string;
  name: string;
  slug: string;
  photo: string;
  price: string;
  primary_category_slug: string
  promotional_price: string;
  categories: Category[];
}

const itemsContainer = ref<HTMLElement | null>(null);
const contentCards = ref<HTMLElement | null>(null);
useSwipe({
  elementRef: itemsContainer,
  transformElementRef: contentCards,
});

const allProducts = ref<Product[]>([]);

const products = ref<Product[]>([]);

const batchSize = 5;
let loadedCount = 0;

const loadMore = () => {
  const nextBatch = allProducts.value.slice(loadedCount, loadedCount + batchSize);
  products.value.push(...nextBatch);
  loadedCount += nextBatch.length;
};

const loadMoreIfNeeded = () => {
  if (loadedCount < allProducts.value.length && currentPosition.value / slideDistance.value + 5 >= products.value.length) {
    loadMore();
  }
};

onMounted(async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}products`);
    const productsData: Product[] = Array.isArray(response.data.results) ? response.data.results : [];

    allProducts.value = productsData.filter((p: Product) =>
      p.categories.some((c: Category) =>
        c.name.toLowerCase().includes('buket') || c.slug.toLowerCase().includes('buket')
      )
    );

    loadMore();

  } catch (error) {
    console.error('Ошибка при получении товаров:', error);
  }
});
</script>

<template>
  <Slider>
    <div class="buket-slider__container" ref="itemsContainer" 
         :style="{ transform: `translateX(${currentPosition}px)` }"
         :class="{ 'is-sliding': isSliding }">
      <div class="slider__items" ref="contentCards">
        <SliderCards 
          @updateWidth="updateWidth" 
          v-for="product in products" 
          :key="product.id" 
          :cardData="product" 
        />
      </div>
    </div>
  </Slider>
</template>

<style scoped>
.buket-slider__container {
  display: flex;
  touch-action: pan-y;
  -webkit-overflow-scrolling: touch;

}
.slider__items{
  display: flex;
  gap: 10px;
}

.is-sliding {
  transition: transform 0.3s ease;
}
</style>
