import { onBeforeRouteUpdate, useRoute } from 'vue-router';
import { onMounted, watch } from 'vue';
import { routeMeta } from './meta';

export function useMetaUpdater() {
  const route = useRoute();

  const updateMeta = (path: string) => {
    const meta = routeMeta[path] || {};
    if (meta.title) {
      document.title = meta.title;
    }
    if (meta.description) {
      updateOrCreateMeta('description', meta.description);
    }
    if (meta.keywords) {
      updateOrCreateMeta('keywords', meta.keywords);
    }
  };

  const updateOrCreateMeta = (name: string, content: string) => {
    let tag = document.querySelector(`meta[name="${name}"]`);
    if (!tag) {
      tag = document.createElement('meta');
      tag.setAttribute('name', name);
      document.head.appendChild(tag);
    }
    tag.setAttribute('content', content);
  };

  onMounted(() => updateMeta(route.path));
  watch(() => route.path, (newPath) => updateMeta(newPath));
  onBeforeRouteUpdate((to) => updateMeta(to.path));
}
