<script setup lang="ts">
import { Rating } from '@/shared/ui/rating';
import { Paragraph } from '@/shared/ui/text/paragraph';
import { useStore } from 'vuex';
import { computed, onMounted, ref } from 'vue';

const store = useStore();

onMounted(() => {
    store.dispatch('reviews/fetchReviews');
});

const reviews = computed(() => store.getters['reviews/reviewsWithShowFlag']);

const toggleReview = (id: string) => {
    store.commit('reviews/toggleShowFull', id);
};
</script>
<template>
    <div class="item" v-for="review in reviews" :key="review.id">
        <div class="item__description">
            <div class="item__title">
                <div class="img">
                    <img :src="review.icon_url" alt="img" />
                </div>
                <div class="name">
                    <Paragraph tagName="p" color="dark" size="xxl">
                        {{ review.company }}
                    </Paragraph>
                    <Paragraph tagName="p" color="dark-gray" size="xs">
                        {{ new Date(review.date).toLocaleDateString('ru-RU') }}
                    </Paragraph>
                </div>
            </div>
            <Paragraph tagName="p" color="dark-gray" size="xs" class="description">
                <template v-if="!review.showFull && review.text.length > 150">
                    {{ review.text.substring(0, 200) }}...
                    <button class="show-more" @click="toggleReview(review.id)">ещё</button>
                </template>
                <template v-else>
                    {{ review.text }}
                    <br />
                    <button v-if="review.text.length > 150" class="show-more" @click="toggleReview(review.id)">
                        свернуть
                    </button>
                </template>
            </Paragraph>
        </div>
        <Rating :value="review.stars" />
    </div>
</template>
<style scoped lang="scss">
@import './style.scss';
</style>