import axios from 'axios';
import { ActionContext } from 'vuex';
import { Banners } from '@/entities/banners/model/banners.types';
import { API_BASE_URL } from '@/shared/api'

interface BannersState {
    bannersData: Banners[];
}

export const bannersModule = {
    namespaced: true,
    state: (): BannersState => ({
        bannersData: []
    }),
    mutations: {
        setBanners(state: BannersState, banners: Banners[]) {
            state.bannersData = banners;
        }
    },
    actions: {
        async fetchBanners({ commit }: ActionContext<BannersState, any>) {
            try {
              const response = await axios.get(`${API_BASE_URL}banners`);
          
              // Берём массив баннеров из response.data.results
              const banners: Banners[] = Array.isArray(response.data.results)
                ? response.data.results
                : [];
          
              commit('setBanners', banners);
            } catch (error) {
              console.error('Ошибка при получении данных баннеров:', error);
            }
          }
          
    },
};