import { ref } from 'vue';

const isDocumentVisible = ref(false);
const documentType = ref<string>('');

export function useOpenDocuments() {
  const openDocument = (type: string) => {
    documentType.value = type;
    isDocumentVisible.value = true;
  };

  const closeDocument = () => {
    isDocumentVisible.value = false;
    documentType.value = '';
  };

  return {
    isDocumentVisible,
    documentType,
    openDocument,
    closeDocument,
  };
}
