import { ref, computed, watch } from 'vue';
import { useStore } from 'vuex';

const isInputVisible = ref(false);

export function useSearch() {
    const store = useStore();
    const searchQuery = ref('');

    const toggleInputVisibility = () => {
        isInputVisible.value = !isInputVisible.value;
    };

    const loadData = async () => {
        const productLoaded = store.state.products.productsData.length > 0;
       

        if (!productLoaded) {
            await store.dispatch('products/fetchProducts');
        }
      
    };

    const performSearch = async () => {
        if (searchQuery.value) {
            await loadData();
            store.dispatch('products/searchProducts', searchQuery.value);
           
        } else {
          
            store.commit('products/setSearchProducts', [])
        }
    };

    watch(searchQuery, performSearch);

    const productSearchResults = computed(() => store.getters['products/searchResults']);
  
    const hasSearchResults = computed(() =>
        productSearchResults.value.length 
       
    );

    return {
        isInputVisible,
        toggleInputVisibility,
        searchQuery,
        hasSearchResults,
        productSearchResults
     
    };
}
