import { HomePage } from '@/pages/main'

export const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage
  },
  {
    path: '/spasibo/',
    name: 'Спасибо',
    component: () => import('@/pages/spasibo/ui/index.vue'),
  },
  {
    path: '/product/',
    name: 'Каталог',
    component: () => import('../../pages/product/ui/product-view/ui/index.vue'),
    children: [
      {
        path: '',
        name: 'Каталог цветов',
        component: () => import('../../pages/product/ui/product-list/ui/index.vue'),
      },
      {
        path: ':primary_category_slug/:slug',
        name: 'Карточка товара',
        component: () => import('../../pages/product/ui/product-card/ui/index.vue'),
        props: true
      },
      {
        path: ':primary_category_slug',
        name: 'Категория товаров',
        component: () => import('../../pages/product/ui/product-info/ui/index.vue'),
        props: true
      }
    ]
  },
  {
    path: '/card/',
    name: 'Корзина',
    component: () => import('@/pages/card/ui/index.vue'),
  },
  {
    path: '/info/',
    name: 'О нас',
    component: () => import('@/pages/info/ui/info-view/index.vue'),
    children: [
      {
        path: 'podpiska/',
        name: 'Цветочная подписка',
        component: () => import('@/pages/info/ui/podpiska/index.vue'),
      },
      {
        path: 'dostavka-cvetov/',
        name: 'Доставка цветов',
        component: () => import('@/pages/info/ui/dostavka/index.vue'),
      },
      {
        path: 'oplata/',
        name: 'Условия оплаты',
        component: () => import('@/pages/info/ui/oplata/index.vue'),
      },
      {
        path: 'usloviya-obmena-i-vozvrata-cvetov/',
        name: 'Условия обмена и возврата цветов',
        component: () => import('@/pages/info/ui/usloviya-obmena-i-vozvrata/index.vue'),
      },
      {
        path: 'contacts/',
        name: 'Контакты',
        component: () => import('@/pages/info/ui/contacts/index.vue'),
      },
      {
        path: 'privacy-policy/',
        name: 'Условия и положения',
        component: () => import('@/pages/info/ui/privacy-policy/index.vue'),
      },
    ]
  },

]
