<script setup lang="ts">
import { Logo } from '@/shared/ui/logo'
import { Icon } from '@/shared/ui/icons'
import { Paragraph } from '@/shared/ui/text/paragraph';
import { FooterSocialLinks } from '@/entities/contacts/ui/footer-links';
import { useBurgerMenuInjection } from '@/widgets/burger-menu/model/useBurgerMenu'; 
import { computed, onMounted } from 'vue';
import { useStore } from 'vuex';

const { isVisibleMenu, closeMenu } = useBurgerMenuInjection();
const store = useStore();

onMounted(() => {
    store.dispatch('categories/fetchCategories');
});

const categories = computed(() => store.state.categories.categoriesData);
const navItems = computed(() => [
  {
    name: 'Каталог',
    link: '/product/',
    children: Array.isArray(categories.value)
      ? categories.value.map((cat: any) => ({
          name: cat.name,
          link: `/${cat.slug}/`
        }))
      : []
  },
  { name: 'Акции', link: '/akcii/' },
  { name: 'О нас', link: '/info/company/' },
  { name: 'Цветочная подписка', link: '/info/podpiska/' },
  { name: 'Доставка цветов', link: '/info/dostavka-cvetov/' },
  { name: 'Контакты', link: '/info/contacts/' }
]);
</script>

<template>
    <div v-if="isVisibleMenu" class="burger__menu">
        <div class="menu__content">
            <div class="content__links">
                <div class="links-top">
                    <logo />
                    <span @click="closeMenu">
                        <Icon type="crossBurger"  />
                    </span>
                </div>
                <ul class="navigation__list">
                    <li v-for="(item, index) in navItems" :key="index" class="item">
                        <RouterLink :to="item.link" tagName="roter-link" class="item__link" @click="closeMenu">
                            <Paragraph tag-name="span" color="dark" class="item__text">
                                {{ item.name }}
                            </Paragraph>
                            <Icon :rotate="-90" type="arrowDown" color="dark" class="rotate-icon"></Icon>
                        </RouterLink>
                    </li>
                </ul>
            </div>
            <div class="contacts__content">
                <div class="social">
                <Paragraph tagName="span" color="dark">
                Подписывайтесь на наши социальные сети:
                </Paragraph>
                    <FooterSocialLinks />

                </div>
            </div>
        </div>
    </div>
</template>
<style scoped lang="scss">
@import './style.scss';
</style>