import axios from 'axios';
import { ActionContext } from 'vuex';
import { Contacts } from '@/entities/contacts/model/contacts.types'; 
import { API_BASE_URL } from '@/shared/api'

interface ContactsState {
    contactsData: Contacts[]; 
}

export const contactsModule = {
    namespaced: true,
    state: (): ContactsState => ({
        contactsData: []  
    }),
    mutations: {
        setContacts(state: ContactsState, contacts: Contacts[]) {
            state.contactsData = contacts;  
        }
    },
    actions: {
        async fetchContacts({ commit }: ActionContext<ContactsState, any>) { 
            try {
                const response = await axios.get(`${API_BASE_URL}contacts`);
                const contacts: Contacts[] = response.data; 
                commit('setContacts', contacts);  
            } catch (error) {
                console.error('Ошибка при получении данных контактов:', error);
            }
        }
    },
    getters: {
        formattedPhone: (state: ContactsState) => {
            const contact = state.contactsData[0];
            if (!contact) return '';  
            return `+ 7 (${contact.phone.slice(2, 5)}) ${contact.phone.slice(5, 8)}-${contact.phone.slice(8, 10)}-${contact.phone.slice(10)}`;
        },
        formattedWorkingHours: (state: ContactsState) => {
            const contact = state.contactsData[0];
            if (!contact) return '';  
            const timeStart = contact.timeStart.split(':').slice(0, 2).join(':');
            const timeEnd = contact.timeEnd.split(':').slice(0, 2).join(':');
            return `часы работы: ${timeStart} - ${timeEnd}`;
        },
        formattedEmail: (state: ContactsState) => {
            const contact = state.contactsData[0];
            if (!contact) return ''; 
            return `email: ${contact.email}`;
        },
        
    }
};
